  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .home__logo {
    max-width: 300px;
    display: block;
    margin: 0 auto;
    padding: 8px; }
  .home .home-section {
    margin-top: 32px;
    max-width: 500px;
    min-width: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center; }
    .home .home-section--separator {
      text-align: center;
      margin: 32px 0 16px;
